body{
    font-family: $font;
}
.navbar-section{
    box-shadow: 1px 1px 7px #e5e5e5;
    background-color: #fff;
    .navbar{
        .navbar-brand{
            img{
                height: 50px;
            }
        }
        .navbar-collapse{
            .navbar-nav{
                padding: 0;
                .nav-item{
                    .nav-link{
                        color: $mainColor;
                        font-weight: bold;
                        font-size: 19px;
                        margin: 0 10px;
                    }
                }
            }
            .social-icons{
                position: relative;
                top: 7px;
                padding: 0;
                li{
                    display: inline-block;
                    margin: 0 3px;
                    a{
                        text-decoration: none;
                        i{
                            height: 42px;
                            width: 42px;
                            text-align: center;
                            font-size: 18px;
                            border: 1px solid #e5e5e5;
                            border-radius: 50%;
                            line-height: 39px;
                            transition: .3s;
                            color: $mainColor;
                            &:hover{
                                background: $mainColor;
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
    }
}