@charset "UTF-8";
body {
  font-family: "Cairo", sans-serif; }

.navbar-section {
  box-shadow: 1px 1px 7px #e5e5e5;
  background-color: #fff; }
  .navbar-section .navbar .navbar-brand img {
    height: 50px; }
  .navbar-section .navbar .navbar-collapse .navbar-nav {
    padding: 0; }
    .navbar-section .navbar .navbar-collapse .navbar-nav .nav-item .nav-link {
      color: #008ec7;
      font-weight: bold;
      font-size: 19px;
      margin: 0 10px; }
  .navbar-section .navbar .navbar-collapse .social-icons {
    position: relative;
    top: 7px;
    padding: 0; }
    .navbar-section .navbar .navbar-collapse .social-icons li {
      display: inline-block;
      margin: 0 3px; }
      .navbar-section .navbar .navbar-collapse .social-icons li a {
        text-decoration: none; }
        .navbar-section .navbar .navbar-collapse .social-icons li a i {
          height: 42px;
          width: 42px;
          text-align: center;
          font-size: 18px;
          border: 1px solid #e5e5e5;
          border-radius: 50%;
          line-height: 39px;
          transition: .3s;
          color: #008ec7; }
          .navbar-section .navbar .navbar-collapse .social-icons li a i:hover {
            background: #008ec7;
            color: #fff; }

.home-page {
  background: url("../assets/Component 2 – 1@2x.png");
  background-size: cover;
  color: #fff; }
  .home-page .home-content {
    display: flex;
    align-items: center; }
    .home-page .home-content .content .logo {
      width: 125px; }
    .home-page .home-content .content h5 {
      color: #ffdf3e; }
    .home-page .home-content .content .home-links img {
      width: 150px;
      margin: 0 7px;
      transition: .3s; }
      .home-page .home-content .content .home-links img:hover {
        transform: scale(1.1); }

.our-services header h3 {
  color: #008ec7; }

.our-services article .service {
  margin-bottom: 30px;
  transition: .3s; }
  .our-services article .service .service-content {
    background-color: #008ec7;
    padding: 50px;
    border-radius: 15px; }
    .our-services article .service .service-content img {
      width: 100%; }
  .our-services article .service h4 {
    color: #008ec7; }
  .our-services article .service:hover {
    transform: translateY(-10px); }

.breadcrumb-section {
  background-color: #00aeef;
  padding: 40px 0; }
  .breadcrumb-section .about-content .breadcrumb {
    background-color: transparent;
    justify-content: center; }
    .breadcrumb-section .about-content .breadcrumb .breadcrumb-item a {
      color: #fff;
      text-decoration: none; }
    .breadcrumb-section .about-content .breadcrumb .breadcrumb-item.active {
      color: #fff;
      font-weight: bold; }
  .breadcrumb-section .about-content .breadcrumb-item + .breadcrumb-item::before {
    display: inline-block;
    padding-right: .5rem;
    padding-left: .5rem;
    color: #fff;
    content: "/"; }

.about-us header h3 {
  color: #008ec7; }

.about-us article .img-section img {
  width: 100%;
  margin-bottom: 20px; }

.about-us article .panel.panel-default .panel-title {
  background-color: #008ec7;
  padding: 4px 17px;
  cursor: pointer; }
  .about-us article .panel.panel-default .panel-title a {
    text-decoration: none;
    color: #fff; }
  .about-us article .panel.panel-default .panel-title i {
    float: left;
    color: #fff;
    position: relative;
    top: 2px; }

.contact header h3 {
  color: #008ec7; }

.contact article .contact-data header h4 {
  color: #008ec7; }
  .contact article .contact-data header h4 span {
    background-color: #fff;
    display: inline-block;
    position: relative;
    z-index: 2; }
  .contact article .contact-data header h4:after {
    background-color: #dedbdb;
    content: "";
    height: 1px;
    left: 0;
    position: absolute;
    top: 20px;
    width: 74%;
    z-index: 1; }

.contact article .contact-data header p {
  line-height: 30px; }

.contact article .contact-data article .list-unstyled {
  padding: 0; }
  .contact article .contact-data article .list-unstyled li {
    line-height: 40px; }
    .contact article .contact-data article .list-unstyled li i {
      padding-left: 7px;
      color: #008ec7; }

.contact article .contact-form header h4 {
  color: #008ec7; }
  .contact article .contact-form header h4 span {
    background-color: #fff;
    display: inline-block;
    position: relative;
    z-index: 2; }
  .contact article .contact-form header h4:after {
    background-color: #dedbdb;
    content: "";
    height: 1px;
    left: 0;
    position: absolute;
    top: 20px;
    width: 86%;
    z-index: 1; }

.contact article .contact-form article button {
  cursor: pointer;
  border: 2px solid #008ec7;
  background: #008ec7;
  color: #fff;
  padding: 3px 42px;
  border-radius: 10px;
  outline: none;
  transition: .3s; }
  .contact article .contact-form article button:hover {
    background-color: #fff;
    color: #008ec7; }

.footer-section {
  background-color: #022533;
  color: #fff;
  padding: 50px 0 20px 0; }
  .footer-section .footer-content {
    border-bottom: 1px solid #fff; }
    .footer-section .footer-content ul {
      padding: 0; }
    .footer-section .footer-content .contact-section ul li {
      line-height: 35px; }
      .footer-section .footer-content .contact-section ul li i {
        padding-left: 10px; }
    .footer-section .footer-content .links-section img {
      width: 100px; }
    .footer-section .footer-content .links-section ul {
      padding: 0;
      margin-top: 20PX; }
      .footer-section .footer-content .links-section ul li {
        display: inline-block;
        margin: 0 5PX; }
        .footer-section .footer-content .links-section ul li i {
          color: #fff;
          font-size: 20px; }
    .footer-section .footer-content .download-section h5 {
      color: #ffdf3e;
      margin-bottom: 20px; }
    .footer-section .footer-content .download-section img {
      width: 135px;
      display: block;
      margin: 15px auto;
      transition: .3s; }
      .footer-section .footer-content .download-section img:hover {
        transform: scale(1.1); }
  .footer-section .copyright p {
    margin-top: 15px; }
