.contact{
    header{
        h3{
            color: $mainColor;
        }
    }
    article{
        .contact-data{
            header{
                h4{
                    color: $mainColor;
                    span{
                        background-color: #fff;
                        display: inline-block;
                        position: relative;
                        z-index: 2;
                    }
                    &:after{
                        background-color: #dedbdb;
                        content: "";
                        height: 1px;
                        left: 0;
                        position: absolute;
                        top: 20px;
                        width: 74%;
                        z-index: 1;
                    }
                }
                p{
                    line-height: 30px;
                }
            }
            article{
                .list-unstyled{
                    padding: 0;
                    li{
                        line-height: 40px;
                        i{
                            padding-left: 7px;
                            color: $mainColor;
                        }
                    }
                }
            }
        }
        .contact-form{
            header{
                h4{
                    color: $mainColor;
                    span{
                        background-color: #fff;
                        display: inline-block;
                        position: relative;
                        z-index: 2;
                    }
                    &:after{
                        background-color: #dedbdb;
                        content: "";
                        height: 1px;
                        left: 0;
                        position: absolute;
                        top: 20px;
                        width: 86%;
                        z-index: 1;
                    }
                }
            }
            article{
                button{
                    cursor: pointer;
                    border: 2px solid $mainColor;
                    background: $mainColor;
                    color: #fff;
                    padding: 3px 42px;
                    border-radius: 10px;
                    outline: none;
                    transition: .3s;
                    &:hover{
                        background-color: #fff;
                        color: $mainColor;
                    }
                }
            }
        }
    }
}