.home-page{
    background: url("../assets/Component 2 – 1@2x.png");
    background-size: cover;
    color: #fff;
    .home-content{
        display: flex;
        align-items: center;
        .content{
            .logo{
                width: 125px;
            }
            h5{
                color: #ffdf3e;
            }
            .home-links{
                img{
                    width: 150px;
                    margin: 0 7px;
                    transition: .3s;
                    &:hover{
                        transform: scale(1.1);
                    }
                }
            }
        }
    }
}

.our-services{
    header{
        h3{
            color: $mainColor;
        }
    }
    article{
        .service{
            margin-bottom: 30px;
            transition: .3s;
            .service-content{
                background-color: $mainColor;
                padding: 50px;
                border-radius: 15px;
                img{
                    width: 100%;
                }
            }
            h4{
                color: $mainColor;
            }
            &:hover{
                transform: translateY(-10px)
            }
        }
    }
}