.footer-section{
    background-color: #022533;
    color: #fff;
    padding: 50px 0 20px 0;
    .footer-content{
        border-bottom: 1px solid #fff;
        ul{
            padding: 0;
        }
        .contact-section{
           ul {
               li {
                    line-height: 35px;
                   i{
                       padding-left: 10px;
                   }
               }
           }
        }
        .links-section{
            img{
                width: 100px;
            }
            ul{
                padding: 0;
                margin-top: 20PX;
                li{
                    display: inline-block;
                    margin: 0 5PX;
                    i{
                        color: #fff;
                        font-size: 20px;
                    }
                }
            }
        }
        .download-section{
            h5{
                color: #ffdf3e;
                margin-bottom: 20px;
            }
            img{
                width: 135px;
                display: block;
                margin: 15px auto;
                transition: .3s;
                &:hover{
                    transform: scale(1.1);
                }
            }
        }
    }
    .copyright{
        p{
            margin-top: 15px;
        }
    }
}