.breadcrumb-section{
    background-color: #00aeef;
    padding: 40px 0;
    .about-content{
        .breadcrumb{
            background-color: transparent;
            justify-content: center;
            .breadcrumb-item{
                a{
                    color: #fff;
                    text-decoration: none;
                }
                &.active{
                    color: #fff;
                    font-weight: bold;
                }
            }
        }
        .breadcrumb-item+.breadcrumb-item::before {
            display: inline-block;
            padding-right: .5rem;
            padding-left: .5rem;
            color: #fff;
            content: "/";
        }
    }
}
.about-us{
    header{
        h3{
            color: $mainColor;
        }
    }
    article{
        .img-section{
            img{
                width: 100%;
                margin-bottom: 20px;
            }
        }
        .panel.panel-default{
            .panel-title{
                background-color: $mainColor;
                padding: 4px 17px;
                cursor: pointer;
                a{
                    text-decoration: none;
                    color: #fff;
                }
                i{
                    float: left;
                    color: #fff;
                    position: relative;
                    top: 2px;
                }
            }
        }
    }
}